.primary {
  color: blue;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  text-align: center;
  height: 50%;
  background-color: bisque;
  position: relative;
  padding: 36px;
  margin: auto;
  border-radius: 25px;
  font-size: 28px;
}

@media only screen and (max-width: 600px) {
  .primary {
    padding: 12px 12px;
    font-size: 20px;
  }
}

.next {
  border: none;
  font-size: 18px;
  text-align: center;
  color: blue;
  background-color: bisque;
  padding: 15px 32px;
  cursor: pointer;
  margin-top: 20px;
  float: right;
}

@media only screen and (max-width: 600px) {
    .next {
      padding: 10px;
      font-size: 14px;
    }
  }
.previous {
  border: none;
  font-size: 18px;
  text-align: center;
  color: blue;
  background-color: bisque;
  padding: 15px 32px;
  cursor: pointer;
  margin-top: 20px;
  float: left;
}

@media only screen and (max-width: 600px) {
    .previous {
      padding: 10px;
      font-size: 14px;
    }
  }

.author {
  color: blue;
  background-color: bisque;
  width: fit-content;
  padding: 10px 10px;
  position: relative;
  margin: auto;
  margin-right: 0px;
  border-radius: 15px;
  margin-top: 10px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  display: flex;
  justify-content: center;
  font-size: 20px;
}

@media only screen and (max-width: 600px) {
    .author {
      padding: 12px 12px;
      font-size: 16px;
    }
  }

.quoteList {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.container {
  width: 546px;
}

@media only screen and (max-width: 600px) {
  .container {
    width: 100%;
    margin-left: 28px;
    margin-right: 28px;
  }
}

.logos {
  border: none;
  position: absolute;
  top: 10px;
  left: 0;
  z-index: 1;
  width: 60px;
}

.logo {
  cursor: pointer;
  height: 50px;
  width: 50px;
}
